import { Modal } from 'antd';
import cx from 'classnames';
import FundiinButton from 'components/Common/FundiinButton';
import { useWindowSize } from 'hooks';
import { cloneDeep } from 'lodash';
import useBrands from 'pages/Brands/hooks/useBrands';
import React, { useEffect, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import { isSmallerPc } from 'utils/helper';
import MenuMobile from './CategoryMobile';
import { Brands, Buys, CategoryType, EStatue, MerchantType } from './const';

const IMG_URL = process.env.REACT_APP_IMG_URL;
const defaultActiveTabs: boolean[] = [];

const Category = ({ defaultActive = 1 }: { defaultActive?: number }) => {
  const { t } = useTranslation();
  const [width] = useWindowSize();
  const { data, isLoading } = useBrands();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [activeTabs, setActiveTabs] = useState<boolean[]>([]);
  const [activeTabs2, setActiveTabs2] = useState<EStatue[]>([]);

  const result = useMemo(() => {
    let activeBrands = Brands;

    if (activeTabs.includes(true)) {
      activeBrands = Brands.filter((_, i) => activeTabs[i]);
    }

    const activeBrandsName = activeBrands.map(b => b.altName);

    const filterMerchants = (d: CategoryType) => {
      return {
        ...d,
        merchants: activeTabs2.some(a => !!a)
          ? d.merchants.filter((m: MerchantType) =>
              activeTabs2.includes(m.runningForm)
            )
          : d.merchants,
      };
    };

    return data
      .filter((d: CategoryType) => activeBrandsName.includes(d.altName))
      .map((d: CategoryType) => filterMerchants(d));
  }, [data, activeTabs, activeTabs2]);

  const count = useMemo(() => {
    let c = 0;
    result.forEach((r: CategoryType) => {
      c += r.merchants.length;
    });
    return c;
  }, [result]);

  const selectedCategory = useMemo(
    () => t(Brands.find((_, i) => !!activeTabs[i])?.text || ''),
    [activeTabs]
  );

  const handleActiveTab = (id: number) => {
    let atTabs = cloneDeep(activeTabs);

    atTabs[id] = !atTabs[id];

    // this line for single choise
    atTabs = atTabs.map((val: boolean, i: number) => (id === i ? val : false));

    setActiveTabs(atTabs);
  };

  const handleActiveTab2 = (id: number, key: EStatue) => {
    const atTabs = cloneDeep(activeTabs2);
    atTabs[id] = atTabs[id] ? EStatue.UNCHECK : key;
    setActiveTabs2(atTabs);
  };

  const handleOpenModel = () => {
    setIsModalOpen(true);
  };

  const handleOk = (vals: boolean[]) => {
    setActiveTabs(vals);
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  useEffect(() => {
    defaultActiveTabs[defaultActive] = true;
    setActiveTabs(defaultActiveTabs);
  }, [defaultActive]);

  return (
    <div className="container">
      <div className="category">
        <div className="category__sidebar">
          <h3 className="category__title">{t('Danh mục')}</h3>
          <div className="category__menu">
            {Brands.map(b => (
              <FundiinButton
                key={b.id}
                className={cx({
                  active: activeTabs[b.id],
                  medium: isSmallerPc(width),
                })}
                onClick={() => handleActiveTab(b.id)}
              >
                {activeTabs[b.id] ? b.aciveIcon : b.icon} {t(b.text)}
              </FundiinButton>
            ))}
          </div>
          <h3 className="category__title">{t('Mua hàng')}</h3>
          <div className="category__menu">
            {Buys.map(b => (
              <FundiinButton
                key={b.id}
                className={cx({
                  medium: isSmallerPc(width),
                  active: activeTabs2[b.id] === b.value,
                })}
                onClick={() => handleActiveTab2(b.id, b.value)}
              >
                {t(b.text)}
              </FundiinButton>
            ))}
          </div>
        </div>
        <div className="category__result">
          <p className="category__count">
            {count} {t('Kết quả')}
          </p>
          <div className="category__sidebar--mobile">
            {Buys.map(b => (
              <FundiinButton
                key={b.id}
                className={cx({
                  mini: true,
                  active: activeTabs2[b.id] === b.value,
                })}
                onClick={() => handleActiveTab2(b.id, b.value)}
              >
                {t(b.text)}
              </FundiinButton>
            ))}
            <FundiinButton
              className={cx({
                mini: true,
                'filter-btn': true,
                active: selectedCategory,
              })}
              onClick={handleOpenModel}
            >
              Danh mục{' '}
              {selectedCategory ? (
                <>
                  (
                  <span
                    className="line-clamp"
                    style={{
                      maxWidth: '82px',
                      textAlign: 'left',
                    }}
                  >
                    {selectedCategory}
                  </span>
                  )
                </>
              ) : (
                ''
              )}
            </FundiinButton>
          </div>
          <div className="category__list">
            {isLoading ? (
              <p>Loading...</p>
            ) : (
              result.map((d: CategoryType) => (
                <React.Fragment key={d.id}>
                  {d.merchants.map((m: MerchantType) => (
                    <div
                      key={`${d.id}-${m.keyTracking}`}
                      className="category__item"
                    >
                      <Link to={m.website}>
                        <img
                          className="cover-img"
                          src={IMG_URL + m.image}
                          alt="item"
                        />
                      </Link>
                      {[EStatue.ONLINE, EStatue.OFFLINE].includes(
                        m.runningForm
                      ) ? (
                        <div
                          className={cx({
                            tag: true,
                            'tag--online': m.runningForm === EStatue.ONLINE,
                            'tag--offline': m.runningForm !== EStatue.ONLINE,
                          })}
                        >
                          <span>{m.runningForm}</span>
                        </div>
                      ) : null}
                    </div>
                  ))}
                </React.Fragment>
              ))
            )}
          </div>
        </div>
      </div>

      <Modal
        width={'100%'}
        open={isModalOpen}
        title={t('category.Danh mục')}
        footer={<div></div>}
        onCancel={handleCancel}
      >
        <MenuMobile activeTabs={activeTabs} onChange={handleOk} />
      </Modal>
    </div>
  );
};

export default Category;
