import Service from 'utils/request';
import { CategoryType } from '../components/Category/const';
import { mockAllBrandService } from './mock';

const env = process.env.NODE_ENV;

export type Response = {
  code: number;
  message: string;
  data: CategoryType[];
};

export const getAllBrandsService = () => {
  if (env === 'development') return mockAllBrandService() as any;
  return Service.get('category/get-following-merchants');
};
